import { Indicator } from "mint-ui";
export default {
  name: "selectService",
  data() {
    return {
      imgUrl: require("@/assets/img/seach.png"),
      value: "",
      count: 0,
      list: [1],
      loading: false
    };
  },

  methods: {
    loadMore() {
      this.loading = true;
      // setTimeout(() => {
      let last = this.list[this.list.length - 1];
      for (let i = 1; i <= 9; i++) {
        this.list.push(last + i);
      }
      this.loading = false;
      // }, 2500);
    }
  }
};



